/*reset style*/
html,
body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
form,
button,
input,
ul,
ol,
li,
dt,
dd,
dl {
  margin: 0;
  padding: 0;
}
html,
body {
  height: 100%;
}
li {
  list-style: none;
}
i {
  font-style: normal;
}
a {
  color: #000;
  text-decoration: none;
}
.wang-clearfix {
  zoom: 1;
}
.wang-clearfix:after {
  content: '';
  display: block;
  clear: both;
}
/*全局样式变量*/
/*设置默认灰色*/
.w-default-color {
  color: #929292;
}
.w-default-bg {
  background: #FFF;
}
/*选中字体颜色*/
.w-f-a-c {
  color: #7CDA7B !important;
}
.w-w-50 {
  width: 50%;
}
.w-w-50-f {
  width: 50%;
  float: left;
}
.w-w-100 {
  width: 100% !important;
}
/*全局视图，高度为window高度 - 底部高度*/
.wang-view {
  width: 100%;
  height: 100%;
  background: #EEEEEE;
  position: absolute;
  left: 0;
  top: 0;
}
.wang-menu {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  padding: 5px 10px;
  background-color: #FFF;
  margin-bottom: 10px;
}
.wang-menu-item {
  -webkit-box-flex: 1;
  -webkit-flex: 1;
          flex: 1;
  padding: 5px 0;
  position: relative;
}
.wang-menu-item img {
  width: 35px;
  height: 35px;
  display: block;
  margin: 0px auto;
}
.wang-menu-item p {
  text-align: center;
  font-size: 14px;
}
.wang-menu-item-badge {
  position: absolute;
  right: 20px;
  top: 2px;
}
.wang-line-bg {
  height: 10px;
  background: #EFEFF4;
}
.wang-ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.wang-banner-item {
  width: 100%;
  height: 180px;
}
.wang-banner-item img {
  width: 100%;
  height: 100%;
}
/*路由过渡动画*/
@-webkit-keyframes routerAnimateSlideIn {
0% {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
}
100% {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
}
}
@keyframes routerAnimateSlideIn {
0% {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
}
100% {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
}
}
/*上拉下拉图标大小*/
.wang-upDownIcon {
  font-size: 24px;
  color: #929292;
}
.wang-detail-box {
  min-height: 300px;
}
.wang-tisp-center {
  text-align: center;
  padding: 20px 0;
}



